<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Sửa thông tin tỉnh thành, quận huyện</h3>
        </div>
        <div class="box-body">
            <form @submit.prevent="handleSubmit">
                <div class="box-body">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label>STT</label>
                                <input v-model="post.stt" name="stt" v-validate="'max:255'" type="text" class="form-control" placeholder="STT...">
                                <span class="error">{{ errors.first("stt") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Tên tỉnh thành, quận huyện</label>
                                <input v-model="post.name" name="name" v-validate="'required|min:3|max:255'" type="text" class="form-control" placeholder="Nhập tên tỉnh thành, quận huyện...">
                                <span class="error">{{ errors.first("name") }}</span>
                            </div>
                            <div class="form-group">
                                <label>url</label>
                                <input v-model="post.url" name="url" v-validate="'required|min:3|max:255'" type="text" class="form-control" placeholder="url...">
                                <span class="error">{{ errors.first("url") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Giá(Giá cho tin ưu tiên tỉnh,quận huyện này)</label>
                                <input v-model="post.price" name="price" v-validate="'max:255'" type="text" class="form-control" placeholder="Nhập giá cho tin ưu tiên...">
                                <span class="error">{{ errors.first("price") }}</span>
                            </div>
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary" >Sửa</button>
                </div>
                <!-- /.box-body -->
            </form>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";
import { Validator } from 'vee-validate';

export default {
    metaInfo: {
        title: "Thêm banner"
    },
    data(){
        return {
            api: this.$store.state.domainApi,
            post:{
                id:0,
                stt:0,
                name: "",
                url: "",
                price:""
            },
            file:{},
            imgPreview:false,
            dictionary:{
                vi: {
                    custom: {
                        // Tủy chỉnh theo từng trường
                        'name' : {
                            required: 'Vui lòng nhập tên banner !',
                            min: 'Tên danh mục tối thiểu 3 ký tự !',
                            max: 'Tên danh mục không được vượt quá 255 ký tự !'
                        },
                        "stt":{
                            required: "Vui lòng chọn hình banner !",
                            image: "Vui lòng chọn định dạng hình ảnh !"
                        },
                        "price":{
                            max: 'Title không được vượt quá 255 ký tự !'
                        }
                    }
                }
            }
        }
    },
    methods: {
        handleSubmit() {
            this.$validator.validate().then(valid => {
                if (valid) {
                    axios.patch(`${this.api}/admin/province`, this.post, {
                        headers:{
                            Authorization: `Bearer ${localStorage.token}`
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "route.admin.province.index"});
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                        
                    });
                    
                }
            });
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');

        axios.get(this.api + "/admin/province/"+this.$route.params.id,{
            headers:{
                Authorization: `Bearer ${localStorage.token}`
            }
        })
        .then(response=>{
            this.post.id = response.data.id;
            this.post.name = response.data.name;
            this.post.url = response.data.url;
            this.post.stt = response.data.stt;
            this.post.price = response.data.price;
        });
    }
}
</script>
